// Utilitaires pour la gestion des images
export const getImageUrl = (imagePath) => {
  if (!imagePath) return null
  
  // Si c'est dÃ©jÃ  une URL complÃ¨te (http/https), la retourner
  if (imagePath.startsWith('http')) return imagePath
  
  // Si c'est un Blob URL, le retourner tel quel
  if (imagePath.startsWith('blob:')) return imagePath
  
  // Si c'est du Base64, le retourner tel quel
  if (imagePath.startsWith('data:')) return imagePath
  
  // Pour GitHub Pages, utiliser le BASE_URL
  const baseUrl = import.meta.env.BASE_URL || '/'
  
  // Nettoyer le chemin
  const cleanPath = imagePath.startsWith('/') ? imagePath.slice(1) : imagePath
  
  return `${baseUrl}${cleanPath}`
}

// Images par dÃ©faut pour les fallbacks
export const defaultImages = {
  logo: 'data:image/svg+xml;base64,',
  
  placeholder: 'data:image/svg+xml;base64,'
}

// Composant Image avec fallback
export const ImageWithFallback = ({ 
  src, 
  alt, 
  fallback = defaultImages.placeholder, 
  className = '',
  onLoad,
  onError,
  ...props 
}) => {
  const handleError = (e) => {
    if (e.target.src !== fallback) {
      e.target.src = fallback;
    }
    
    // Appeler le onError personnalisÃ© s'il existe
    if (onError) onError(e);
  }

  const handleLoad = (e) => {
    // Appeler le onLoad personnalisÃ© s'il existe
    if (onLoad) onLoad(e);
  }

  const imageUrl = getImageUrl(src) || fallback;
  
  // Debug supprimÃ© pour production

  return (
    <img
      src={imageUrl}
      alt={alt}
      className={className}
      onError={handleError}
      onLoad={handleLoad}
      {...props}
    />
  )
}
